.id-carousel-wrapper {
  background: white;
  position: relative;
  padding-top: 100px;
  padding-bottom: 100px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  ul {
    position: absolute;
    top: 50%;
    width: 800px;
    height: 200px;
    left: 50%;
    margin-left: -400px;
    margin-top: -130px;
  }

  li {
    width: 25%;
    list-style-type: none;
    position: absolute;
    top: 20%;
    padding: 20px;
    height: 200px;
    opacity: 0;
    padding-top: 40px;
    text-align: center;
    transition: 1s opacity;
  }
}

.carousel-image {
  width: 190px;
  height: 190px;
  object-fit: cover;
  border-radius:50%;
  // width: 60px;
  // height: 60px;

}
